import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React from "react"

class Router extends StreamlitComponentBase<State> {
  componentDidMount = () => {
    console.log(window.location)
    console.log(this.props.args)

    switch (this.props.args.method) {
      case "set":
        let new_path = this.props.args.new_path.toString()
        if (new_path.charAt(0) != "/") {
          new_path = `/${new_path}`
        }
        const path = "/#" + new_path
        console.log(`navigating to ${window.location.origin + path}`)
        window.location.assign(window.location.origin + path)
        window.history.pushState({}, "", window.location.origin + path)
        break
      case "get":
        let route = "/"
        try {
          const url = window.location.search.substr(14)
          const decoded = decodeURIComponent(url)
          route = decoded.split("/#")[1]
        } catch (error) {
          route = this.props.args.default
        }
        Streamlit.setComponentValue(route)

        break

      default:
        break
    }

    Streamlit.setComponentReady()
  }

  render = () => <div></div>
}

export default withStreamlitConnection(Router)
